import React from 'react'
import Link from 'gatsby-link'

import MainMenu from '../components/menu'
import me from '../../public/img/me.png'


const IndexPage = ({data}) => (
  <div className="child-page">
      <MainMenu />

    <div className="ui container">
      <div id="info-segment" className="ui segment">
        <img id="avatar" className="ui small floated circular image" src={me} alt="Me" />
        {/* <h2 className="index-header-title"></h2> */}
          <p className="index-body-text">
            I spent the first twenty years of my life disengaged, misguided, and desprate to fit in. I didn't have the courage to do anything extraordinary and I outright lacked direction. I didn't code from a young age. Two years into college, I was failing out of my business finance degree. Suddenly, life dealt me a series of blows to the face. Tradegy struck, I lost the two people closest to my heart. I had no choice but to reflect on everything that had happened in my life up to that point. I had to re-evaluate everything. I moved back in with my parents, concieved a well-defined life purpose, and decided I wanted to study computer science. I started improving myself everyday, I started reading books, and I started surrounding myself with winners.<br/><br/>

            Fast forward two years, I brought my GPA up to a 3.5, I have my dream internship as a software engineer at the Royal Bank of Canada's Innovation Lab, I'm organizing a hackathon for the biggest university in the nation, I started writing, I started making art, I've been plant-based for over a year. I feel the best I've ever felt, life is good. I'm determined to lead, to teach, and to help others reach breakthroughs in all domains of life. I'm destined to make a massive impact in this world, and the momentum is building. If you're interested in my story, please, reach out. I'd love to hear yours.
          </p>
      </div>
    </div>

    <div className="ui container page-body">
      <div id="blog-body" className="ui segment">
        <h4 id="blog-entry">
          <Link to="/posts/twenties">You May Never See Me Again</Link> <i>14 Aug 2018</i>
          <div className="ui divider" />
          <Link to="/posts/first-day-tips">First Day CS Internship: Complete Guide</Link> <i>26 Jul 2018</i>
          <div className="ui divider" />
          <Link to="/posts/if-you-think">If You Think You Have Your Life Together</Link> <i>12 Jul 2018</i>
        </h4>
      </div>
    </div>
  </div>
)

export default IndexPage



